import React, { Component } from "react";

import IntlMessages from "../../../../components/utility/intlMessages";
// import Button from "../../../../components/uielements/button";
import { Button, Col, Modal, Row, Alert } from "antd";
import EditableTableMultiPayment from "./editableTableMultiPayment";
// import NumberFormat from "../../../App/NumberFormat";

class ModalMultiPayment extends Component {
  state = {
    payments: [],
    paymentDetails: {
      payments: []
    },
    totalAmount: this.props.totalAmount,
    paymentCategories: [],
    changed: false,
    limit: 0
  };

  componentDidUpdate = () => {
    const { limit } = this.state;
    const { payments } = this.props;

    if (Array.isArray(payments) && payments.length > 0 && limit === 0) {
      this.setState({
        payments,
        limit: 1
      });
    }
  };

  handleSaveMultiPaymentList = payments => {
    this.setState({
      paymentDetails: {
        payments: [...payments]
      },
      changed: true
    });
  };

  clearMultiPayments = payments => {
    this.setState({
      paymentDetails: {
        payments: [...payments]
      },
      payments: []
    });
  };

  handleSubmit = e => {
    // e.preventDefault();
    const { setErrPayment } = this.props;
    const { paymentDetails } = this.state;

    // validasi payment term
    let index = [];
    paymentDetails.payments.map((payment, key) => {
      if (payment.payment_type === 2 && !payment.id_payment_term) {
        return index.push(key);
      }
    });
    if (index.length > 0) {
      return setErrPayment([...index]);
    } else {
      setErrPayment([]);
    }

    const data = Object.assign({}, paymentDetails);

    this.setState({ changed: false });
    // console.log("data", data);
    this.props.submit(data);
  };

  render() {
    const { paymentTerms, totalAmount } = this.props;
    // console.log("dataSource ", dataSource);

    return (
      <Modal
        centered
        closable={false}
        footer={
          <Row justify="end" type="flex">
            <Col>
              <Button
                onClick={() => {
                  this.props.handleModal();
                }}
              >
                <IntlMessages id="close" />
              </Button>

              <Button
                type="primary"
                disabled={
                  this.props.status > 1 ||
                  this.props.notExist > 0 ||
                  !this.props.permission
                    ? true
                    : false
                }
                onClick={this.handleSubmit}
              >
                <IntlMessages id="button.ok" />
              </Button>
            </Col>
          </Row>
        }
        maskClosable={false}
        title={<IntlMessages id="paymentMethod" />}
        visible={this.props.visible}
        width="60%"
      >
        {this.state.changed ? (
          <Alert
            message={<IntlMessages id="salesInvoices.saveMultiPayment" />}
            type="warning"
            showIcon
            style={{ marginBottom: "10px" }}
          />
        ) : (
          ""
        )}

        <EditableTableMultiPayment
          payments={this.state.payments}
          handleSaveMultiPaymentList={this.handleSaveMultiPaymentList}
          clearMultiPayments={this.clearMultiPayments}
          totalAmount={totalAmount}
          paymentTerms={paymentTerms}
          errPayment={this.props.errPayment}
          status={this.props.status}
        />
        {/* <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
          <Col span={15} style={{ marginTop: "25px", textAlign: "right" }}>
            <strong>
              <IntlMessages id="paidAmount" />
            </strong>
          </Col>
          <Col span={8} offset={1} style={{ marginTop: "25px" }}>
            <span>
              <NumberFormat value={paidAmount} />
            </span>
          </Col>
        </Row> */}
        {/* <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
          <Col span={15} style={{ marginTop: "5px", textAlign: "right" }}>
            <strong>
              <IntlMessages id="totalAmount" />
            </strong>
          </Col>
          <Col span={8} offset={1} style={{ marginTop: "5px" }}>
            <span>
              <NumberFormat value={totalAmount} />
            </span>
          </Col>
        </Row> */}
        {/* <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
          <Col span={15} style={{ marginTop: "5px", textAlign: "right" }}>
            <Strong
              error={
                parseFloat(parseFloat(this.state.outstanding).toFixed(2)) !==
                0
              }
            >
              {parseFloat(this.state.outstanding) < 0 
                ? (<IntlMessages id="outstanding" />)
                : (<IntlMessages id="overPayment" />)}
            </Strong>
          </Col>
          <Col span={8} offset={1} style={{ marginTop: "5px" }}>
            <Span
              error={
                parseFloat(parseFloat(this.state.outstanding).toFixed(2)) !==
                0
              }
            >
              {Math.abs(parseFloat(outstanding))
                .toFixed(2)
                .replace(/\B(?=(\d{3})+(?!\d))/g, ",")}
            </Span>
          </Col>
        </Row> */}
      </Modal>
    );
  }
}

export default ModalMultiPayment;
