import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Input, InputNumber, Select, Col, Row, Modal, Icon, Tooltip, Popconfirm, Empty } from "antd";
import actions from "../../../../redux/raptor/raptorSalesInvoice/actions";

const { updateItemGrouping } = actions;
const Option = Select.Option;
const InputGroup = Input.Group;

let id = 0;

class ModalItemGrouping extends Component {

  componentDidUpdate = (prevProps) => {
    const { success } = this.props.raptorSalesInvoice;
    const { selectedItem } = this.props;
    if (success) {
      this.props.form.resetFields();
      this.props.handleModal();
    }

    if (prevProps.selectedItem !== selectedItem && selectedItem) {
      let child_item_group = Array.isArray(selectedItem.child_items)
        ? selectedItem.child_items
        : [];
      let array = [];
      const { form } = this.props

      child_item_group.map((data, index) => {
        array.push(index);
      })
      form.setFieldsValue({
        keys: array,
      });

      id=child_item_group.length;
    }
  }

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        const { form, selectedItem, updateItemGrouping } = this.props;
        const keys = form.getFieldValue('keys');
        let item_groups = [];

        if(keys.length > 0){
          values.id_item_child.map((data, index) => {
            item_groups.push({
              id_item_child: values.id_item_child[index],
              quantity: values.quantity[index]
            })
          })
        }

        const data = { item_groups };
        updateItemGrouping(
          selectedItem.id_sales_invoice,
          selectedItem.id_item,
          data
        );
      }
    });
  };

  removeChild = (k) => {
    const { form } = this.props;
    // can use data-binding to get
    const keys = form.getFieldValue('keys');

    // can use data-binding to set
    form.setFieldsValue({
      keys: keys.filter(key => key !== k),
      [`id_item_child[${k}]`]: undefined
    });
  }

  addChild = () => {
    const { form } = this.props;
    // can use data-binding to get
    const keys = form.getFieldValue('keys');
    
    const nextKeys = keys.concat(id++);
    // can use data-binding to set
    // important! notify form to detect changes
    form.setFieldsValue({
      keys: nextKeys,
    });
  }

  handleChangeCompositionItem = (index, e) => {
    const { inventory_items } = this.props.raptorSalesInvoice
    const {setFieldsValue} = this.props.form

    const item = inventory_items.find(data => data.id_item === e);
    setFieldsValue ({
      [`unit_name[${index}]`] : item.base_unit.unit_name 
    })
  }

  render() {
    // console.log(this.props);
    const { getFieldDecorator, getFieldValue, resetFields } = this.props.form;
    const { handleModal, selectedItem } = this.props;
    const {
      raptorSalesInvoice,
      inventory_items
    } = this.props.raptorSalesInvoice;
    const { loadingSubmit, loadingForeign } = this.props.raptorSalesInvoice;
    // console.log(units);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    const formItemLayoutWithOutLabel = {
      wrapperCol: {
        xs: { span: 17, offset: 7 },
        sm: { span: 17, offset: 7 },
      },
    };

    getFieldDecorator('keys', { initialValue: [] });
    const keys = getFieldValue('keys');
    const formItems = keys.map((k, index) => (
      //console.log(data.unit_conversion[k])
      <InputGroup
        key={k}
      >
        <Col span={14}>  
          <Form.Item
            {...formItemLayout}
            label={<IntlMessages id="items.itemChild" />}
            key={k}
            extra={
              selectedItem.child_items && selectedItem.child_items[k] &&
              parseFloat(selectedItem.child_items[k].quantity) >
              parseFloat(selectedItem.child_items[k].current_qty)
                ? (
                    <span style={{ color: "orange", fontSize: ".9em" }}>
                      <IntlMessages 
                        id="currentQty"
                        values={{
                          current: parseFloat(selectedItem.child_items[k].current_qty),
                          unit: selectedItem.child_items[k].base_unit_code
                        }}
                      />
                    </span>
                  )
                : null
            }
          >
            {getFieldDecorator(`id_item_child[${k}]`, {
              initialValue: selectedItem.child_items && selectedItem.child_items[k]
                ? selectedItem.child_items[k].id_item_child 
                : null,
              rules: [{
                required: true,
                message: <IntlMessages id="required" />,
              }],
            })(
              <Select
                  loading={loadingForeign}
                  showSearch
                  style={{ width:"100%" }}
                  onChange={this.handleChangeCompositionItem.bind(this, k)}
                  optionFilterProp="children"
                >
                  {inventory_items ? inventory_items.map((data, key) => (
                    getFieldValue(`id_item_child[${k}]`) === data.id_item 
                      ? (
                          <Option key={key} value={data.id_item}>
                            {data.item_code + " - " + data.item_name}
                          </Option>
                        )
                      : Array.isArray(getFieldValue('id_item_child')) && 
                      !getFieldValue('id_item_child').includes(data.id_item)
                        ? (
                            <Option key={key} value={data.id_item}>
                              {data.item_code + " - " + data.item_name}
                            </Option>
                          )
                        : null
                  )) : null}
                </Select>
            )}
          
          </Form.Item>
        </Col>
        <Col span={4}>  
          <Form.Item
            {...formItemLayout}
            label={<IntlMessages id="unit" />}
            key={k}
          >
            {getFieldDecorator(`unit_name[${k}]`, {
              initialValue: selectedItem.child_items && selectedItem.child_items[k]
                ? selectedItem.child_items[k].base_unit_code
                : null,
              rules: [{
                required: true,
                message: <IntlMessages id="required" />,
              }],
            })(<Input style={{ width:"100%" }} disabled={true} />)}
          
          </Form.Item>
        </Col>
        <Col span={4}>
          <Form.Item
            {...formItemLayout}
            label={<IntlMessages id="quantity" />}
            key={k}
          >
            {getFieldDecorator(`quantity[${k}]`, {
              initialValue: selectedItem.child_items && selectedItem.child_items[k]
                ? selectedItem.child_items[k].quantity 
                : null,
              rules: [{
                required: true,
                message: <IntlMessages id="required" />,
              }],
            })(
              <InputNumber
                formatter={value =>
                  `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                }
                parser={value => value.replace(/\$\s?|(,*)/g, "")}
                min={0}
                style={{ width: "100%" }}
              />
            )}  
          </Form.Item>
        </Col>
        <Col span={2}>
          <Popconfirm
            title={<IntlMessages id="confirmDelete" />}
            onConfirm={() => this.removeChild(k)}
            okText={<IntlMessages id="yes" />}
            cancelText={<IntlMessages id="no" />}
          >
            <Tooltip
              title={<IntlMessages id="delete" />}
            >
              <Button
                className="dynamic-delete-button"
                icon="delete"
                style={{ marginTop:"27px"}}
                shape="circle"
                type="danger"
                disabled={getFieldValue("keys").length === 1}
              />
            </Tooltip>
          </Popconfirm>
        </Col>         
      </InputGroup>
    ));

    return (
      <Modal
        title={<IntlMessages id="adjustGrouping" />}
        visible={this.props.visible}
        centered
        width="60%"
        onCancel={() => {
          resetFields();
          handleModal();
        }}
        footer={
          <Row>
            <Col>
              <Button 
                loading={loadingSubmit}
                onClick={() => {
                  resetFields();
                  handleModal();
                }} 
              >
                <IntlMessages id="button.cancel" />
              </Button>
              <Button
                type="primary"
                onClick={this.handleSubmit}
                disabled={raptorSalesInvoice ? raptorSalesInvoice.status > 1 : false}
                loading={loadingSubmit}
              >
                <IntlMessages id="button.ok" />
              </Button>
            </Col>
          </Row>
        }
      >
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          <Row type="flex" justify="start" gutter={6} style={{ height: "450px", overflowY: "auto" }}>
            <Col span={24}>
              {formItems.length > 0
                ? formItems
                : (
                  <Empty 
                    description={<IntlMessages id="noData" />}
                    style={{ marginBottom: "12px" }}
                  />
                )}

              <Form.Item {...formItemLayoutWithOutLabel}>
                <Button type="dashed" onClick={this.addChild} style={{ width: '60%' }}>
                  <Icon type="plus" /> {<IntlMessages id="addItemChild" />}
                </Button>
              </Form.Item>
            </Col>
          </Row>
        </Form>
      </Modal>
    );
  }
}

const WrappedModalItemGrouping = Form.create({ name: "adjust_item_grouping_modal" })(
  ModalItemGrouping
);

const mapStateToProps = state => {
  return { raptorSalesInvoice: state.raptorSalesInvoice };
};

export default compose(
  connect(
    mapStateToProps,
    { updateItemGrouping }
  )
)(WrappedModalItemGrouping);
