import React, { Component } from "react";
// import moment from "moment";

import {
  // Button,
  Form
  // Popconfirm,
  // Tooltip
} from "antd";
import EditableCell from "./editableCellMultiPayment";
import EditableContext from "./editableContext";
import "../../../App/editableStyle.css";
// import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";
import CardWrapper from "../../../App/app.style";

const EditableRow = ({ form, index, ...props }) => (
  <EditableContext.Provider value={form}>
    <tr {...props} />
  </EditableContext.Provider>
);

const EditableFormRow = Form.create()(EditableRow);

class EditableTable extends Component {
  constructor(props) {
    super(props);

    this.state = {
      dataSource: []
    };
  }

  componentDidMount() {
    const { payments, clearMultiPayments } = this.props;

    if (Array.isArray(payments) && payments.length > 0) {
      let dataSource = [];
      payments.map((payment, key) => {
        let init = {};
        init.key = key;
        init.id_sales_invoice_payment = payment.id_sales_invoice_payment;
        init.payment_category_code =
          payment.payment_category.payment_category_code;
        init.payment_type = payment.payment_type;
        init.id_payment_term = payment.id_payment_term;
        init.payment_term_code = payment.payment_term_code
          ? payment.payment_term_code
          : "-";
        init.payment_amount = parseFloat(payment.payment_amount);

        dataSource.push(init);
      });

      this.setState({ dataSource });
      clearMultiPayments(dataSource);
    }
  }

  componentDidUpdate() {
    // console.log("updated, prev-> ", this.prevProps.salesOrders);
    const { payments, clearMultiPayments } = this.props;

    if (Array.isArray(payments) && payments.length > 0) {
      let dataSource = [];
      payments.map((payment, key) => {
        let init = {};
        init.key = key;
        init.id_sales_invoice_payment = payment.id_sales_invoice_payment;
        init.payment_category_name = payment.payment_category_name;
        init.payment_type = payment.payment_type;
        init.id_payment_term = payment.id_payment_term;
        init.payment_term_code = payment.payment_term_code
          ? payment.payment_term_code
          : "-";
        init.payment_amount = parseFloat(payment.payment_amount);

        dataSource.push(init);
      });

      this.setState({ dataSource });
      clearMultiPayments(dataSource);
    }
  }

  handleSave = row => {
    const { handleSaveMultiPaymentList } = this.props;
    const newData = this.state.dataSource;

    const index = newData.findIndex(currData => row.key === currData.key);

    newData.splice(index, 1, {
      ...newData[index],
      ...row
    });

    this.setState({
      dataSource: [...newData]
    });

    // console.log("new Datas", newData);
    handleSaveMultiPaymentList(newData);
  };

  render() {
    // console.log(this.state.dataSource);
    const { paymentTerms } = this.props;
    const { dataSource } = this.state;
    const components = {
      body: {
        row: EditableFormRow,
        cell: EditableCell
      }
    };

    const raw_columns = [
      {
        title: getLanguage({ id: "paymentCategoryCode" }),
        dataIndex: "payment_category_code",
        rowKey: "payment_category_code",
        width: "15%"
      },
      {
        title: getLanguage({ id: "type" }),
        dataIndex: "payment_type",
        rowKey: "payment_type",
        width: "10%",
        render: text => {
          if (text === 1) {
            return getLanguage({ id: "cash" });
          } else if (text === 2) {
            return getLanguage({ id: "credit" });
          } else if (text === 3) {
            return getLanguage({ id: "card" });
          } else if (text === 4) {
            return getLanguage({ id: "other" });
          }
        }
      },
      {
        title: getLanguage({ id: "sidebar.paymentTerm" }),
        dataIndex: "payment_term_code",
        rowKey: "payment_term_code",
        editable: true,
        width: "10%"
      },
      {
        title: getLanguage({ id: "paymentAmount" }),
        dataIndex: "payment_amount",
        rowKey: "payment_amount",
        width: "15%",
        align: "right",
        render: text => <NumberFormat value={text} />
      }
    ];

    const columns = raw_columns.map(col => {
      if (!col.editable) {
        return col;
      } else {
        return {
          ...col,
          onCell: record => ({
            record,
            editable: record.payment_type === 2 ? col.editable : false,
            dataIndex: col.dataIndex,
            title: col.title,
            paymentTerms: paymentTerms,
            handleSave: this.handleSave,
            errPayment: this.props.errPayment
          })
        };
      }
    });

    // const info = [
    //   {
    //     payment_amount: paidAmount,
    //     key: dataSource.length > 0 ? dataSource.length + 1 : "",
    //     payment_category_code: "Paid Amount"
    //   },
    //   {
    //     payment_amount: totalAmount,
    //     key: dataSource.length > 0 ? dataSource.length + 2 : "",
    //     payment_category_code: "Total Amount"
    //   },
    //   {
    //     payment_amount: paidAmount - totalAmount < 0 ? 0 : paidAmount - totalAmount,
    //     key: dataSource.length > 0 ? dataSource.length + 3 : "",
    //     payment_category_code: "Change Amount"
    //   }
    // ];
    // const modifiedData = dataSource.length > 0 ? [...dataSource, ...info] : [];
    return (
      <CardWrapper>
        <div>
          <Scrollbars style={{ width: "100%" }}>
            <TableWrapper
              className="invoiceListTable"
              components={components}
              rowKey={record => record.key}
              rowClassName={() => "editable-row"}
              dataSource={dataSource}
              columns={columns}
              pagination={false}
              // scroll={{ x: 1300 }}
            />
          </Scrollbars>
        </div>
      </CardWrapper>
    );
  }
}

export default EditableTable;
