import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Input, Select, Col, Row, Modal, TreeSelect } from "antd";
import actions from "../../../../redux/raptor/raptorSalesInvoice/actions";

const { storeItem } = actions;
const Option = Select.Option

class ModalItem extends Component {
  state = {
    id_location: [],
    default_location: null,
    location_error: null,
    limit: false
  }

  componentDidUpdate = () => {
    const { limit } = this.state;
    const { success, raptorSalesInvoice } = this.props.raptorSalesInvoice;

    if (success) {
      this.setState({ id_location: [], location_error: null });
      this.props.form.resetFields();
      this.props.handleModal();
    }

    if (raptorSalesInvoice && raptorSalesInvoice.id_location && !limit) {
      this.setState({
        default_location: raptorSalesInvoice.id_location,
        limit: true
      });
    }
  }
  
  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        const { default_location } = this.state;
        const { raptorSalesInvoice } = this.props.raptorSalesInvoice;
        values.id_location = [
          ...this.state.id_location,
          default_location
        ];
        values.base_unit_measure_by = 1;
        values.unit_conversions = [];
        this.props.storeItem(raptorSalesInvoice.id_sales_invoice, values);
      }
    });
  };

  handleChangeItemLocation = (e) => {
    const { locations } = this.props.raptorSalesInvoice
      ? this.props.raptorSalesInvoice
      : [];
    const { default_location, location_error } = this.state;

    if (location_error) {
      this.setState({
        location_error: null
      });
    }

    if (e.includes("all")) {
      let arr_locations = [];
      locations.map(data => data.id_location !== default_location 
        ? arr_locations.push(data.id_location)
        : null
      );
      this.setState({ id_location: arr_locations });
    } else {
      if (e.length === 0) {
        this.setState({
          location_error: <IntlMessages id="required" />,
        });
      }

      const filtered = e.filter(id => id !== default_location);

      this.setState({ id_location: filtered });
    }
  }

  render() {
    // console.log(this.props);
    const { default_location } = this.state;
    const { getFieldDecorator, getFieldError, resetFields } = this.props.form;
    const { handleModal, selectedItem } = this.props;
    const {
      raptorSalesInvoice,
      units,
      locations,
      item_categories
    } = this.props.raptorSalesInvoice;
    const { loadingSubmit, loadingForeign } = this.props.raptorSalesInvoice;
    const error = this.props.raptorSalesInvoice.errorField 
      ? this.props.raptorSalesInvoice.errorField 
      : [];
    const loop = array =>
      array.map(data => {
        let perData = {
            title : `${data.item_category_code} (${data.item_category_name})`,
            value : data.id_item_category,
            key : data.id_item_category,
            children : data.children_active ? loop(data.children_active) : null
          }

        return perData;
      });
    // console.log(units);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    return (
      <Modal
        title={<IntlMessages id="items.button.add" />}
        visible={this.props.visible}
        centered
        width="50%"
        onCancel={() => {
          this.setState({ id_location: [], location_error: null });
          resetFields();
          handleModal();
        }} 
        footer={
          <Row>
            <Col>
              <Button 
                loading={loadingSubmit}
                onClick={() => {
                  this.setState({ id_location: [], location_error: null });
                  resetFields();
                  handleModal();
                }} 
              >
                <IntlMessages id="button.cancel" />
              </Button>
              <Button
                type="primary"
                onClick={this.handleSubmit}
                disabled={raptorSalesInvoice ? raptorSalesInvoice.status > 1 : false}
                loading={loadingSubmit}
              >
                <IntlMessages id="button.ok" />
              </Button>
            </Col>
          </Row>
        }
      >
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          <Row type="flex" justify="start" gutter={6}>
            <Col span={12}>  
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="items.code" />}
                validateStatus={
                  error.item_code || getFieldError("item_code")
                    ? "error"
                    : null
                }
                help={
                  error.item_code
                    ? error.item_code
                    : getFieldError("item_code")
                }
              >
                {getFieldDecorator("item_code", {
                  initialValue: selectedItem.item_code,
                  rules: [
                    {
                      max: 30,
                      message: <IntlMessages id="maxLength30" />
                    }
                  ]
                })(
                  <Input disabled={true}/>
                )}
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="items.name" />}
              >
                {getFieldDecorator("item_name", {
                  initialValue: selectedItem.item_name,
                  rules: [
                    {
                      max: 50,
                      message: <IntlMessages id="maxLength50" />
                    }
                  ]
                })(
                  <Input disabled={true} />
                )}
              </Form.Item>
            </Col>

            <Col span={24}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="itemCategories.pageHeader" />}
              >
                {getFieldDecorator("id_item_category", {
                  //initialValue: 0,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <TreeSelect
                    style={{ width: "100%" }}
                    allowClear={true}
                    showSearch={true}
                    treeNodeFilterProp="title"
                    dropdownStyle={{ maxHeight: 300, overflow: 'auto' }}
                    treeData={item_categories ? loop(item_categories) : []}
                    treeDefaultExpandAll
                    loading={loadingForeign}
                  />
                )}
              </Form.Item>
            </Col>

            <Col span={12}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="items.itemType" />}
              >
                {getFieldDecorator("item_type", {
                  //initialValue: 0,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Select
                    showSearch
                    style={{ width: "100%" }}
                    optionFilterProp="children"
                    filterOption={true}
                    allowClear={true}
                    loading={loadingSubmit}
                  >
                    <Option value={1}>Inventory</Option>
                    {/* <Option value={2}>Non Inventory</Option> */}
                    <Option value={3}>Grouping</Option>
                    <Option value={4}>Service</Option>
                  </Select>
                    
                )}
              </Form.Item>
            </Col>

            <Col span={12}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="items.baseUnit" />}
              >
                {getFieldDecorator("id_base_unit", {
                  //initialValue: 0,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Select
                    loading={loadingForeign}
                    showSearch
                    onChange={this.handleChangeBaseUnit}
                    optionFilterProp="children"
                    style={{ width: "100%" }}
                  >
                    {units ? units.map((data, key) => (
                      <Option key={key} value={data.id_unit}>
                        {data.unit_code + " (" + data.unit_name + ")"}
                      </Option>
                    )) : null}
                  </Select>
                )}
              </Form.Item>
            </Col>

            <Col span={24}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="items.locations" />}
                required
                validateStatus={
                  this.state.id_location_error 
                    ? "error"
                    : null
                }
                help={
                  this.state.id_location_error
                }
              >
                <Select
                  mode="multiple"
                  loading={loadingForeign}
                  showSearch
                  allowClear={true}
                  optionFilterProp="children"
                  onChange={this.handleChangeItemLocation}
                  value={[
                    default_location,
                    ...this.state.id_location
                  ]}
                  //disabled={used ? true : false}
                >
                  <Option key={-1} value={"all"}> {<IntlMessages id="select.all.location" />}</Option>
                  {locations ? locations.map((data, key) => (
                    <Option key={key} value={data.id_location} disabled={data.id_location === default_location}>
                      {data.location_name}
                    </Option>
                  )) : null}
                </Select>
              </Form.Item>
            </Col>
          </Row>
        </Form>
      </Modal>
    );
  }
}

const WrappedModalItem = Form.create({ name: "add_item_modal" })(
  ModalItem
);

const mapStateToProps = state => {
  return { raptorSalesInvoice: state.raptorSalesInvoice };
};

export default compose(
  connect(
    mapStateToProps,
    { storeItem }
  )
)(WrappedModalItem);
