import React, { Component } from "react";
import { Form, InputNumber, Select } from "antd";
import EditableContext from "./editableContext";
import IntlMessages from "../../../../components/utility/intlMessages";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";

const FormItem = Form.Item;
const Option = Select.Option;

class EditableCell extends Component {
  state = {
    editing: false
  };

  toggleEdit = () => {
    const editing = !this.state.editing;

    this.setState({ editing }, () => {
      if (editing) {
        this.input.focus();
      }
    });
  };

  handleClickOutside = e => {
    const { editing } = this.state;

    if (editing && !this.cell) {
      this.toggleEdit();
    } else if (
      editing &&
      this.cell !== e.target &&
      !this.cell.contains(e.target)
    ) {
      this.save();
      document.removeEventListener("click", this.handleClickOutside, true);
    }
  };

  save = (name, id) => {
    const { record, paymentTerms, handleSave } = this.props;

    this.form.validateFields((error, values) => {
      if (error) {
        return;
      }

      this.toggleEdit();

      if (name && name === "payment_term_code") {
        const paymentTerm = paymentTerms.find(
          currData => currData.id_payment_term === id
        );

        const newRecord = Object.assign({}, record, {
          id_payment_term: paymentTerm ? paymentTerm.id_payment_term : null,
          payment_term_code: paymentTerm ? paymentTerm.payment_term_code : "-"
        });

        handleSave(newRecord);
      } else {
        handleSave({ ...record, ...values });
      }
    });
  };

  render() {
    const { editing } = this.state;
    const {
      editable,
      dataIndex,
      title,
      record,
      index,
      paymentTerms,
      errPayment,
      handleSave,
      // status,
      ...restProps
    } = this.props;
    return (
      <td ref={node => (this.cell = node)} {...restProps}>
        {editable && record ? (
          <EditableContext.Consumer>
            {form => {
              this.form = form;
              if (editing && dataIndex === "payment_term_code") {
                return (
                  <FormItem style={{ margin: 0 }}>
                    {form.getFieldDecorator(dataIndex, {
                      initialValue: record.id_payment_term
                    })(
                      <Select
                        ref={node => (this.input = node)}
                        showSearch
                        style={{ width: "100%" }}
                        onSelect={this.save.bind(this, "payment_term_code")}
                        optionFilterProp="children"
                        filterOption={true}
                      >
                        {paymentTerms
                          .filter(data => data.is_cash === 0)
                          .map((data, key) => (
                            <Option
                              key={data.id_payment_term}
                              value={data.id_payment_term}
                            >
                              {data.payment_term_code}
                            </Option>
                          ))}
                      </Select>
                    )}
                  </FormItem>
                );
              } else if (editing && dataIndex === "payment_amount") {
                document.addEventListener(
                  "click",
                  this.handleClickOutside,
                  true
                );
                return (
                  <FormItem style={{ margin: 0 }}>
                    {form.getFieldDecorator(dataIndex, {
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ],
                      initialValue: record[dataIndex]
                    })(
                      <InputNumber
                        ref={node => (this.input = node)}
                        style={{ width: "100%" }}
                        onPressEnter={this.save}
                        formatter={value =>
                          `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                        }
                        parser={value => value.replace(/\$\s?|(,*)/g, "")}
                        min={0.01}
                        precision={2}
                      />
                    )}
                  </FormItem>
                );
              } else if (!editing && dataIndex === "payment_term_code") {
                return (
                  <>
                    <div
                      className={`editable-cell-value-wrap ${
                        errPayment.includes(record.key) &&
                        !record.id_payment_term
                          ? "error"
                          : ""
                      }`}
                      onClick={this.toggleEdit}
                    >
                      {record[dataIndex]}
                    </div>
                    {errPayment.includes(record.key) &&
                    !record.id_payment_term ? (
                      <span style={{ color: "#f5222d", fontSize: ".9em" }}>
                        {getLanguage({ id: "required" })}
                      </span>
                    ) : null}
                  </>
                );
              } else if (!editing && dataIndex === "payment_amount") {
                return (
                  <div
                    className="editable-cell-value-wrap"
                    onClick={this.toggleEdit}
                  >
                    {record[dataIndex] ? (
                      <NumberFormat value={record[dataIndex]} />
                    ) : (
                      "0"
                    )}
                  </div>
                );
              } else {
                return (
                  <div
                    className="editable-cell-value-wrap"
                    onClick={this.toggleEdit}
                  >
                    {restProps.children}
                  </div>
                );
              }
            }}
          </EditableContext.Consumer>
        ) : (
          restProps.children
        )}
      </td>
    );
  }
}

export default EditableCell;
