import React, { Component } from "react";

import { Tooltip, Form } from "antd";
import EditableCell from "./editableCell";
import EditableContext from "./editableContext";
import "../../../App/editableStyle.css";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import Button from "../../../../components/uielements/button";

import {
  // getBaseQty,
  getDiscountBillPerItem,
  // getItemPrice,
  getNettPurchPrice,
  getSubTotalPerItem,
  getSubTotalTaxPerItem,
  getSubTotalTaxPerItemInclusive,
  getUnitConversion,
  getItemDiscountDM,
  getSalesPriceAndDiscountNonDM
} from "../../../../helpers/helper";
import NumberFormat from "../../../App/NumberFormat";

import ModalItem from "./modalItem";
import ModalActivateItem from "./modalActivateItem";
import ModalItemGrouping from "./modalGrouping";
import getLanguage from "../../../App/MyLanguange";

const EditableRow = ({ form, index, ...props }) => (
  <EditableContext.Provider value={form}>
    <tr {...props} />
  </EditableContext.Provider>
);

const EditableFormRow = Form.create()(EditableRow);

class EditableTable extends Component {
  constructor(props) {
    super(props);
    const { items } = props;

    this.state = {
      dataSource: [...items],
      count: items.length + 1,
      totalQty: 0,
      discount_pct: 0,
      discount_amount: 0,
      newListItems: [],
      total_discount: 0,
      total_tax: 0,
      total_exclude_discount_tax: 0,
      sum_subtotal_discount_item: 0,
      visible: false,
      visibleActivate: false,
      visibleGrouping: false,
      selectedItem: {}
    };
  }

  componentDidUpdate(prevProps) {
    const {
      clear,
      clearItems,
      handleSaveItemList,
      items,
      loadingItems,
      loadingTransactions,
      exceededItems,
      status
    } = this.props;

    if (
      !loadingTransactions &&
      !loadingItems &&
      (items.length !== 0)
    ) {
      let newData = [];
      let newTotQty = 0;
      let newIndex = 0;
      let new_sum_subtotal_discount_item = 0;
      let not_exist = 0;

      items.map((item, index) => {
        let init = {};
        // let disc = 0;

        init.index_no = index + 1;
        init.id_sales_invoice = item.id_sales_invoice
          ? item.id_sales_invoice
          : null;
        init.id_sales_invoice_detail = item.id_sales_invoice_detail
          ? item.id_sales_invoice_detail
          : null;

        init.id_item = item.id_item;
        init.id_item_category = item.item
          ? item.item.id_item_category
          : undefined;
        init.item_type = item.item ? item.item.item_type : undefined;
        init.item_status = item.item_status;
        init.item_code = item.item_code;
        init.sku = item.item ? item.item.sku : undefined;
        init.sku_name = item.item ? item.item.sku_name : undefined;
        init.item_name = item.item_name;
        init.item_locations = item.item
          ? Array.isArray(item.item.item_locations)
            ? item.item.item_locations.map(data => data["id_location"])
            : []
          : [];

        init.qty = parseFloat(item.qty);
        init.item_price = parseFloat(item.item_price);

        init.id_unit = item.id_unit;
        init.unit_code = item.unit ? item.unit.unit_code : item.unit_code;

        init.id_base_unit = item.id_base_unit
          ? item.id_base_unit
          : item.id_unit;
        init.base_unit_code = item.base_unit
          ? item.base_unit.unit_code
          : item.base_unit_code;
        init.base_qty = parseFloat(item.base_qty);

        init.discount_amount = item.discount_amount;
        init.discount = init.discount_amount;

        init.sub_total = parseFloat(item.sub_total);
        init.sub_total_cost = parseFloat(item.sub_total_cost);

        init.netto_sales_price = null;

        if (
          Array.isArray(exceededItems) &&
          exceededItems.length > 0 &&
          status === 1
        ) {
          console.log("updated here");
          const getExceeded = exceededItems.find(
            data => data.id_item === init.id_item
          );
          if (getExceeded) {
            init.current_qty = parseFloat(getExceeded.current_qty);
            init.is_group_item = getExceeded.is_group_item;
            init.child_items = Array.isArray(getExceeded.child_items)
              ? [...getExceeded.child_items]
              : null;
            init.message = getExceeded.message;
          }
        }

        newTotQty += parseFloat(init.base_qty);
        newIndex = parseInt(init.index_no) + 1;
        new_sum_subtotal_discount_item += getSubTotalPerItem(
          init.item_price,
          init.qty,
          init.discount
        );

        // count not exist item
        if (!init.id_item) not_exist++;

        newData.push(init);
      });

      // console.log(newData);
      this.setState({
        dataSource: [...newData],
        totalQty: newTotQty,
        count: newIndex,
        sum_subtotal_discount_item: new_sum_subtotal_discount_item
      });
      clearItems(newData, newTotQty, new_sum_subtotal_discount_item, not_exist);
    }
    // clear item jika location/supplier berubah
    else if (clear) {
      this.setState({
        dataSource: [],
        count: 1,
        sum_subtotal_discount_item: 0
      });
      handleSaveItemList([], 0, 0, true);
    }
  }

  handleDelete = index_no => {
    const { handleSaveItemList } = this.props;
    let newTotQty = 0;

    const dataSource = this.state.dataSource;

    const deleted = dataSource.find(currData => currData.index_no === index_no);
    let newData = null;
    if (deleted.id_delivery_order) {
      newData = dataSource.filter(
        currData =>
          currData.index_no !== index_no &&
          currData.id_delivery_order !== deleted.id_delivery_order
      );
      if (newData.length === 0) {
        this.props.handleDeleteDOItems();
      }
    } else {
      newData = dataSource.filter(currData => currData.index_no !== index_no);
    }

    let new_sum_subtotal_discount_item = 0;
    newData.map((currData, index) => {
      currData.index_no = index + 1;
      newTotQty += parseFloat(currData.base_qty);
      new_sum_subtotal_discount_item += getSubTotalPerItem(
        currData.item_price,
        currData.qty,
        currData.discount
      );
    });

    this.setState({
      dataSource: [...newData],
      count: newData.length + 1,
      totalQty: newTotQty,
      sum_subtotal_discount_item: new_sum_subtotal_discount_item
    });
    handleSaveItemList(
      newData,
      newTotQty,
      new_sum_subtotal_discount_item,
      true,
      true
    );
  };

  handleSave = row => {
    const {
      custDisc,
      discountItems,
      handleSaveItemList,
      inclusive_tax
    } = this.props;
    const newData = this.state.dataSource;
    let newTotQty = 0;

    const index = newData.findIndex(
      currData => row.index_no === currData.index_no
    );
    // --------------- discount management
    row = getItemDiscountDM(row, discountItems);
    // --------------- /discount management

    let price_list = getSalesPriceAndDiscountNonDM(row, 1);
    if (!row.id_discount) {
      row.id_discount = null;
      row.discount = price_list.discount;
    }

    // get customer pricelist
    if (row.id_unit !== newData[index].id_unit) {
      row.item_price = price_list.price;
    }

    // Ubah price dan base_qty
    row.unit_conv = getUnitConversion(row.id_unit, row.unit_conversion);
    row.base_qty = parseFloat(row.qty) * row.unit_conv;
    // row.item_price = getItemPrice(row);
    row.sub_total = getSubTotalPerItem(row.item_price, row.qty, row.discount);

    // generate sub_total_tax
    let sub_total_tax;
    if (inclusive_tax) {
      sub_total_tax = getSubTotalTaxPerItemInclusive(
        row,
        newData,
        custDisc,
        inclusive_tax
      );
    } else {
      sub_total_tax = getSubTotalTaxPerItem(
        row,
        newData,
        custDisc,
        inclusive_tax
      );
    }
    row.sub_total_tax = sub_total_tax;

    row.netto_sales_price = getNettPurchPrice(
      row,
      newData,
      inclusive_tax,
      custDisc
    );
    row.bill_discount = getDiscountBillPerItem(row, newData, custDisc);
    row.bill_discount = row.bill_discount || 0;

    newData.splice(index, 1, {
      ...newData[index],
      ...row
    });

    let new_sum_subtotal_discount_item = 0;
    newData.map(currData => {
      newTotQty += parseFloat(currData.base_qty);
      new_sum_subtotal_discount_item += getSubTotalPerItem(
        currData.item_price,
        currData.qty,
        currData.discount
      );
    });

    this.setState({
      dataSource: [...newData],
      totalQty: newTotQty,
      sum_subtotal_discount_item: new_sum_subtotal_discount_item
    });
    handleSaveItemList(
      newData,
      newTotQty,
      new_sum_subtotal_discount_item,
      false,
      true
    );
  };

  handleModal = () => {
    this.setState({ visible: false, selectedItem: {} });
  };

  handleModalActivate = () => {
    this.setState({ visibleActivate: false, selectedItem: {} });
  };

  handleModalGrouping = () => {
    this.setState({ visibleGrouping: false, selectedItem: {} });
  };

  render() {
    // console.log(this.state.dataSource);

    const { dataSource } = this.state;
    const { loadingTable } = this.props;
    const components = {
      body: {
        row: EditableFormRow,
        cell: EditableCell
      }
    };

    const raw_columns = [
      {
        title: getLanguage({ id: "no." }),
        dataIndex: "index_no",
        width: "5%"
        // fixed: "left"
      },
      {
        title: getLanguage({ id: "code" }),
        dataIndex: "item_code",
        width: "15%",
        // fixed: "left",
        render: (text, row) => {
          // button add item
          if (!row.id_item && this.props.permissions.includes("Create Item")) {
            return (
              <Button
                onClick={() => {
                  this.setState({
                    visible: true,
                    selectedItem: row
                  });
                }}
              >
                <IntlMessages id="items.button.add" />
              </Button>
            );
          } else if (
            row.item_status === -1 &&
            this.props.permissions.includes("Update Item")
          ) {
            return (
              <Button
                onClick={() => {
                  this.setState({
                    visibleActivate: true,
                    selectedItem: row
                  });
                }}
              >
                <IntlMessages id="tooltip.activate" />
              </Button>
            );
          }

          if (
            row.is_group_item &&
            this.props.permissions.includes("Update Item Grouping")
          ) {
            return (
              <Button
                onClick={() => {
                  this.setState({
                    visibleGrouping: true,
                    selectedItem: row
                  });
                }}
              >
                <IntlMessages id="adjustGrouping" />
              </Button>
            );
          }

          if (text && text.length > 15) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 15) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "itemName" }),
        dataIndex: "item_name",
        width: "20%",
        render: (text, row) => {
          if (text && text.length > 25) {
            return row.id_item ? (
              <>
                <Tooltip placement="bottom" title={text}>
                  {text.substring(0, 25) + "..."}
                </Tooltip>
                <br />
                <Tooltip placement="bottom" title={row.message}>
                  <span style={{ color: "orange", fontSize: ".9em" }}>
                    {row.message ? `${row.message.substring(0, 25)}...` : null}
                  </span>
                </Tooltip>
              </>
            ) : (
              <>
                <Tooltip placement="bottom" title={text}>
                  {text.substring(0, 25) + "..."}
                </Tooltip>
                <br />
                <Tooltip
                  placement="bottom"
                  title={<IntlMessages id="warningItemNotExist" />}
                >
                  <span style={{ color: "orange", fontSize: ".9em" }}>
                    <IntlMessages id="warningItemNotExist">
                      {text => text.substring(0, 25) + "..."}
                    </IntlMessages>
                  </span>
                </Tooltip>
              </>
            );
          } else {
            return row.id_item ? (
              <>
                <span>{text}</span>
                <br />
                <Tooltip placement="bottom" title={row.message}>
                  <span style={{ color: "orange", fontSize: ".9em" }}>
                    {row.message ? `${row.message.substring(0, 25)}...` : null}
                  </span>
                </Tooltip>
              </>
            ) : (
              <>
                <span>{text}</span>
                <br />
                <Tooltip
                  placement="bottom"
                  title={<IntlMessages id="warningItemNotExist" />}
                >
                  <span style={{ color: "orange", fontSize: ".9em" }}>
                    <IntlMessages id="warningItemNotExist">
                      {text => text.substring(0, 25) + "..."}
                    </IntlMessages>
                  </span>
                </Tooltip>
              </>
            );
          }
        }
      },
      {
        title: getLanguage({ id: "quantity" }),
        dataIndex: "qty",
        width: "10%",
        align: "right",
        render: (text, row) => (
          <>
            <NumberFormat value={text} /> {row.unit_code}
          </>
        )
      },
      {
        title: (
          <Tooltip placement="top" title={<IntlMessages id="baseQuantity" />}>
            <a href="javascript:;">
              <IntlMessages id="baseQty" />{" "}
            </a>
            {/* <br />
            <Icon type="question-circle" /> */}
          </Tooltip>
        ),
        width: "10%",
        dataIndex: "base_qty",
        align: "right",
        render: (text, row) => (
          <>
            <NumberFormat value={text} /> {row.base_unit_code}
          </>
        )
      },
      {
        title: getLanguage({ id: "price" }),
        dataIndex: "item_price",
        width: "10%",
        align: "right",
        editable: false,
        render: text => <NumberFormat value={text} />
      },
      {
        title: getLanguage({ id: "discount" }),
        dataIndex: "discount",
        width: "5%",
        align: "right",
        editable: false,
        render: text => {
          if (text.slice(-1) === "%") return text;
          else return <NumberFormat value={text} />;
        }
      },
      {
        title: getLanguage({ id: "subTotal" }),
        dataIndex: "sub_total",
        width: "15%",
        // editable: true,
        align: "right",
        render: text => <NumberFormat value={text} />
      }
    ];

    const columns = raw_columns.map(col => {
      if (!col.editable) {
        return col;
      }
      return {
        ...col,
        onCell: record => ({
          record,
          editable: col.editable,
          dataIndex: col.dataIndex,
          title: col.title,
          taxes: this.props.taxes,
          status: this.props.status,
          permissions: this.props.permissions,
          handleSave: this.handleSave
        })
      };
    });
    return (
      <>
        <ModalItem
          handleModal={this.handleModal}
          visible={this.state.visible}
          selectedItem={this.state.selectedItem}
        />

        <ModalActivateItem
          handleModal={this.handleModalActivate}
          visible={this.state.visibleActivate}
          selectedItem={this.state.selectedItem}
        />

        <ModalItemGrouping
          handleModal={this.handleModalGrouping}
          visible={this.state.visibleGrouping}
          selectedItem={this.state.selectedItem}
        />

        <Scrollbars style={{ width: "100%" }}>
          <TableWrapper
            components={components}
            rowKey={record => record.index_no}
            rowClassName={() => "editable-row"}
            dataSource={dataSource}
            columns={columns}
            loading={loadingTable}
            pagination={false}
            // scroll={{ x: 1300 }}
          />
        </Scrollbars>
      </>
    );
  }
}

export default EditableTable;
