const actions = {
  getRaptorSalesInvoices: (page, search, filter) => ({
    type: "GET_RAPTOR_SALES_INVOICES_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  synchronizeRaptor: () => ({
    type: "SYNCHRONIZE_RAPTOR_SALES_INVOICES_REQUEST"
  }),
  editRaptorSalesInvoice: (id_sales_invoice, search) => ({
    type: "EDIT_RAPTOR_SALES_INVOICE_REQUEST",
    payload: { id_sales_invoice: id_sales_invoice, search: search }
  }),
  updateRaptorSalesInvoice: (id_sales_invoice, data) => ({
    type: "UPDATE_RAPTOR_SALES_INVOICE_REQUEST",
    payload: {
      id_sales_invoice: id_sales_invoice,
      body: data
    }
  }),
  storeItem: (id_sales_invoice, data) => ({
    type: "STORE_ITEM_RAPTOR_SALES_INVOICE_REQUEST",
    payload: { id_sales_invoice, body: data }
  }),
  updateItem: (id_sales_invoice, data) => ({
    type: "UPDATE_ITEM_RAPTOR_SALES_INVOICE_REQUEST",
    payload: { id_sales_invoice, body: data }
  }),
  updateItemGrouping: (id_sales_invoice, id_item, data) => ({
    type: "UPDATE_ITEM_GROUPING_RAPTOR_SALES_INVOICE_REQUEST",
    payload: { id_sales_invoice, id_item, body: data }
  }),
  confirmRaptorSalesInvoice: (id_sales_invoice, data) => ({
    type: "CONFIRM_RAPTOR_SALES_INVOICE_REQUEST",
    payload: { id_sales_invoice, body: data }
  }),
  // confirm multiple SI
  confirmRaptorSalesInvoices: (data, page, search, filter) => ({
    type: "CONFIRM_RAPTOR_SALES_INVOICES_REQUEST",
    payload: { body: data,
      page: page,
      search: search,
      filter: filter }
  }),
  cancelRaptorSalesInvoice: (id_sales_invoice, data) => ({
    type: "CANCEL_RAPTOR_SALES_INVOICE_REQUEST",
    payload: { id_sales_invoice, body: data }
  }),
  uploadDocument: (id_sales_invoice, data) => ({
    type: "UPLOAD_RAPTOR_SALES_INVOICE_DOCUMENT_REQUEST",
    payload: {
      id_sales_invoice,
      body: data
    }
  }),
  downloadDocument: id_raptor_sales_invoice_document => ({
    type: "DOWNLOAD_RAPTOR_SALES_INVOICE_DOCUMENT_REQUEST",
    payload: { id_raptor_sales_invoice_document }
  }),
  deleteDocument: (id_raptor_sales_invoice_document, id_sales_invoice) => ({
    type: "DELETE_RAPTOR_SALES_INVOICE_DOCUMENT_REQUEST",
    payload: {
      id_raptor_sales_invoice_document: id_raptor_sales_invoice_document,
      id_sales_invoice: id_sales_invoice
    }
  }),
  setPrintData: data => ({
    type: "SET_PRINT_DATA_RAPTOR_SALES_INVOICE",
    payload: { body: data }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearWarning: () => ({
    type: "CLEAR_WARNING"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  }),
  setTaxBase: tax_bases => ({
    type: "SET_TAX_BASES_RAPTOR_SALES_INVOICE",
    payload: { tax_bases: tax_bases }
  })
};

export default actions;
