import React, { Component } from "react";
import moment from "moment";
import { connect } from "react-redux";
import { compose } from "redux";
import actions from "../../../../redux/raptor/raptorSalesInvoice/actions";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import {
  Col,
  DatePicker,
  Divider,
  Empty,
  Form,
  Icon,
  Input,
  // InputNumber,
  Modal,
  Popconfirm,
  Row,
  Select,
  Steps,
  Tabs,
  Tooltip
} from "antd";

import EditableTable from "./editableTable";
import ModalMultiPayment from "./modalMultiPayment";
import ModalCancel from "./modalCancel";
import RaptorSalesInvoiceDocUpdateForm from "./updateDocForm";
// import { Object } from "core-js";
import { customDot, StatusTag } from "../../../App/app.style";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";

const { Option } = Select;
const { TextArea } = Input;
const InputGroup = Input.Group;
const Step = Steps.Step;
const TabPane = Tabs.TabPane;
const warning = Modal.warning;

const {
  setPrintData,
  confirmRaptorSalesInvoice,
  cancelRaptorSalesInvoice,
  unconfirmRaptorSalesInvoice
} = actions;

// const payments = (data) => [
