class RaptorSalesInvoiceUpdateForm extends Component {
  state = {
    activeCustDisc: null,
    clear: false,
    currency_rate: 1,
    customer: {},
    discount_amount: 0,
    err: false,
    errInvAmount: false,
    errPayment: [],
    fullDiscMultiplier: null,
    idCustomer: null,
    idLocation: null,
    items: [],
    itemDetails: {
      sales_invoice_details: []
    },
    payments: [],
    paymentDetails: {
      payments: []
    },
    exceededItems: [],
    limit: 0,
    tab: null,
    sum_subtotal_discount_item: 0,
    not_exists: 0,
    viewed: false,
    visible: false,
    visibleModalCancel: false,
    visibleMultiPayment: false,
    tab: "1"
  };

  componentDidMount = () => {
    const { info } = this.props.raptorSalesInvoice;
    // when ordered qty is exceeded
    if (Array.isArray(info) && info.length > 0) {
      this.setState({ exceededItems: info });
    }
  };

  componentDidUpdate = (prevProps, prevState) => {
    const { itemDetails, not_exists, viewed } = this.state;
    const { info, warning_message } = this.props.raptorSalesInvoice;
    const { loading, raptorSalesInvoice } = this.props.raptorSalesInvoice;

    if (
      !loading &&
      prevState.itemDetails.sales_invoice_details.length === 0 &&
      this.state.limit === 0
    ) {
      let payments = [];
      if (raptorSalesInvoice.payments) {
        payments = raptorSalesInvoice.payments.map(element => {
          const {
            payment_categories,
            payment_terms
          } = this.props.raptorSalesInvoice;

          let data = { ...element };

          // cari payment term untuk get payment term code yang sesuai
          if (!data.payment_term_code) {
            const findPayTerm = payment_terms.find(
              data => data.id_payment_term === element.id_payment_term
            );
            if (findPayTerm) {
              data.payment_term_code = findPayTerm.payment_term_code;
            } else data.payment_term_code = "-";
          }

          // return
          return data;
        });
      }
      this.setState({
        idCustomer: raptorSalesInvoice ? raptorSalesInvoice.id_customer : null,
        customer: raptorSalesInvoice ? raptorSalesInvoice.customer : null,
        idLocation: raptorSalesInvoice ? raptorSalesInvoice.id_location : null,
        items: raptorSalesInvoice
          ? raptorSalesInvoice.sales_invoice_details
          : [],
        payments: payments,
        paymentDetails: {
          payments: raptorSalesInvoice ? raptorSalesInvoice.payments : []
        },
        limit: 1
      });
    }

    if (not_exists > 0 && !viewed && warning_message && warning_message.title) {
      this.setState({ viewed: true }, () => this.showWarningItem());
    }

    if (
      prevState.itemDetails.sales_invoice_details !==
      itemDetails.sales_invoice_details
    ) {
      const { setPrintData } = this.props;
      setPrintData(itemDetails);
    }

    // when ordered qty is exceeded
    if (
      Array.isArray(info) &&
      info.length > 0 &&
      info !== prevProps.raptorSalesInvoice.info
    ) {
      this.setState({
        exceededItems: info,
        items: this.state.itemDetails.sales_invoice_details
      });
    }
  };

  clearItems = (items, total_qty, sum_subtotal_discount_item, not_exists) => {
    this.setState({
      items: [],
      itemDetails: { sales_invoice_details: items },
      total_qty: total_qty,
      sum_subtotal_discount_item,
      not_exists,
      clear: false
    });
  };

  setErrPayment = errPayment => {
    this.setState({ errPayment });
  };

  handleModalMultiPayment = () => {
    this.setState({
      visibleMultiPayment: !this.state.visibleMultiPayment
    });
  };

  handleModalCancel = () => {
    this.setState({
      visibleModalCancel: !this.state.visibleModalCancel
    });
  };

  handleSaveItemList = (
    items,
    total_qty,
    sum_subtotal_discount_item,
    sync = false,
    dataChanged = false
  ) => {
    const setStateData = {
      itemDetails: { sales_invoice_details: items },
      total_qty: total_qty,
      sum_subtotal_discount_item,
      clear: false
    };
    this.setState(setStateData);
  };

  handleOkMultiPayment = payments => {
    this.setState({
      paymentDetails: payments,
      visibleMultiPayment: false
    });
  };

  showWarningItem = () => {
    const { warning_message } = this.props.raptorSalesInvoice;

    warning({
      title: warning_message.title,
      content: warning_message.content,
      icon: <Icon type="info-circle" />,
      okText: "OK",
      onOk: () => {}
    });
  };

  render() {
    // console.log(this.props);
    const { getFieldDecorator, getFieldValue, getFieldError } = this.props.form;

    const { raptorSalesInvoice } = this.props;
    const {
      base_currency,
      cash_banks,
      customers,
      locations,
      payment_terms
    } = raptorSalesInvoice ? raptorSalesInvoice : [];

    const { user } = this.props;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];
    const {
      loading,
      loadingCancel,
      loadingConfirm,
      loadingForeign,
      loadingItems,
      loadingTransactions
      // loadingTable
    } = raptorSalesInvoice ? raptorSalesInvoice : [];
    const data = raptorSalesInvoice.raptorSalesInvoice
      ? raptorSalesInvoice.raptorSalesInvoice
      : [];

    const transactionStatus = () => {
      let curr = 0;
      switch (data.status) {
        case 1:
          curr = 0;
          break;
        case 2:
          curr = 1;
          break;
        case 3:
          curr = 2;
          break;
        default:
          curr = 0;
          break;
      }
      return curr;
    };

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <Form layout="vertical">
        <ModalMultiPayment
          errPayment={this.state.errPayment}
          handleModal={this.handleModalMultiPayment}
          notExist={this.state.not_exists}
          payments={this.state.payments}
          paymentTerms={payment_terms}
          permission={permissions.includes("Confirm Raptor Sales Invoice")}
          submit={this.handleOkMultiPayment}
          setErrPayment={this.setErrPayment}
          status={data.status}
          totalAmount={data.total_amount}
          visible={this.state.visibleMultiPayment}
        />

        <ModalCancel
          handleModal={this.handleModalCancel}
          submit={this.props.handleSubmitCancelSI}
          visible={this.state.visibleModalCancel}
          loading={loadingCancel}
        />

        <Row style={{ margin: "12px 0px" }}>
          <Col span={12}>
            <Steps
              size="small"
              progressDot={customDot}
              current={transactionStatus()}
            >
              <Step title={getLanguage({ id: "pending" })} />
              <Step title={getLanguage({ id: "processed" })} />
              <Step title={getLanguage({ id: "returned" })} />
            </Steps>
          </Col>
        </Row>

        <Tabs
          defaultActiveKey="1"
          onTabClick={key => this.setState({ tab: key })}
        >
          <TabPane
            tab={<IntlMessages id="purchaseInvoices.tabs.detail" />}
            key="1"
          >
            <Row>
              <Col span={12} style={{ paddingRight: "15px" }}>
                <InputGroup>
                  <Col span={12}>
                    <Form.Item
                      {...formItemLayout}
                      label={<IntlMessages id="salesInvoice.no" />}
                    >
                      {getFieldDecorator("sales_invoice_no", {
                        initialValue: data.sales_invoice_no
                      })(<Input disabled={true} />)}
                    </Form.Item>
                  </Col>
                  <Col span={12}>
                    <Form.Item
                      {...formItemLayout}
                      label={
                        <IntlMessages id="purchaseInvoices.transactionDate" />
                      }
                      style={{ marginBottom: 0, paddingBottom: 0 }}
                    >
                      {getFieldDecorator("transaction_date", {
                        initialValue: moment(
                          data.transaction_date,
                          "YYYY-MM-DD HH:mm:ss"
                        )
                      })(
                        <DatePicker
                          style={{ width: "100%" }}
                          showTime
                          format="DD MMMM YYYY HH:mm:ss"
                          disabled={true}
                          allowClear={false}
                          disabledDate={e => e.valueOf() > moment()}
                        />
                      )}
                    </Form.Item>
                  </Col>
                </InputGroup>

                <Col span={24}>
                  <InputGroup>
                    <Col span={12}>
                      <Form.Item
                        {...formItemLayout}
                        label={
                          <Tooltip
                            placement="top"
                            title={<IntlMessages id="salesInvoices.location" />}
                          >
                            <IntlMessages id="purchaseInvoices.location" />
                            <Icon
                              style={{ marginLeft: "5px" }}
                              type="question-circle"
                              theme="filled"
                            />
                          </Tooltip>
                        }
                      >
                        {getFieldDecorator("id_location", {
                          initialValue: data.id_location,
                          rules: [
                            {
                              required: true,
                              message: <IntlMessages id="required" />
                            }
                          ]
                        })(
                          <Select
                            disabled={true}
                            showSearch
                            style={{ width: "100%" }}
                            placeholder={
                              <IntlMessages id="purchaseInvoices.location" />
                            }
                            loading={loadingForeign}
                            optionFilterProp="children"
                            filterOption={true}
                          >
                            {locations && locations.length > 0
                              ? locations.map((data, key) => (
                                  <Option key={key} value={data.id_location}>
                                    {`${data.location_code} (${data.location_name})`}
                                  </Option>
                                ))
                              : null}
                          </Select>
                        )}
                      </Form.Item>
                    </Col>
                    <Col span={12}>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="sidebar.customer" />}
                      >
                        {getFieldDecorator("id_customer", {
                          initialValue: data.id_customer,
                          rules: [
                            {
                              required: true,
                              message: <IntlMessages id="required" />
                            }
                          ]
                        })(
                          <Select
                            disabled={true}
                            showSearch
                            style={{ width: "100%" }}
                            placeholder={
                              <IntlMessages id="salesOrders.filter.select.customer" />
                            }
                            loading={loadingForeign}
                            notFoundContent={
                              <Empty
                                description={<IntlMessages id="noData" />}
                              />
                            }
                            optionFilterProp="children"
                            filterOption={true}
                          >
                            {customers && customers.length > 0
                              ? customers.map((data, key) => (
                                  <Option key={key} value={data.id_customer}>
                                    {`${data.customer_code} (${data.customer_name})`}
                                  </Option>
                                ))
                              : null}
                          </Select>
                        )}
                      </Form.Item>
                    </Col>
                  </InputGroup>
                </Col>
              </Col>
              <Col span={12} style={{ paddingLeft: "15px" }}>
                <Form.Item
                  {...formTextArea}
                  label={<IntlMessages id="purchaseInvoices.note" />}
                >
                  {getFieldDecorator("note", {
                    initialValue: data.note
                  })(<TextArea autosize={{ minRows: 4 }} disabled={true} />)}
                </Form.Item>
              </Col>
            </Row>

            <Divider orientation="left" style={{ fontSize: 14 }}>
              <IntlMessages id="raptorSalesInvoices.divider.item" />
            </Divider>

            <Row style={{ marginBottom: "40px" }}>
              <Col span={24}>
                <EditableTable
                  clear={this.state.clear}
                  clearItem={this.clearItem}
                  clearItems={this.clearItems}
                  custDisc={this.state.custDisc}
                  handleSaveItemList={this.handleSaveItemList}
                  item={this.state.item}
                  items={this.state.items}
                  exceededItems={this.state.exceededItems}
                  loadingItems={loadingItems}
                  loadingTransactions={loadingTransactions}
                  permissions={permissions}
                  status={data.status}
                />
              </Col>
              <Col span={12}>
                <Col span={10} style={{ marginTop: "15px", textAlign: "left" }}>
                  <strong>
                    <IntlMessages id="purchaseInvoices.totalQty" />
                  </strong>
                  <strong>
                    <IntlMessages id="bsUnt">{txt => ` (${txt})`}</IntlMessages>
                  </strong>
                </Col>
                <Col span={13} offset={1} style={{ marginTop: "15px" }}>
                  <strong>
                    <NumberFormat value={this.state.total_qty} />
                  </strong>
                </Col>
              </Col>
            </Row>

            <Row>
              <Col span={12}>
                <Row
                  type="flex"
                  justify="start"
                  style={{ marginBottom: "35px" }}
                >
                  <Col span={12} style={{ marginTop: "5px" }}>
                    <label className="">
                      <IntlMessages id="purchaseInvoices.taxInvoiceNo" />
                    </label>
                  </Col>
                  <Col span={11} offset={1}>
                    {getFieldDecorator("tax_invoice_no", {
                      initialValue: data.tax_invoice_no
                    })(
                      <Input
                        disabled={true}
                        placeholder={getLanguage({
                          id: "purchaseInvoices.taxInvoiceNo"
                        })}
                        size="small"
                        style={{
                          marginTop: "3px",
                          borderColor: getFieldError("tax_invoice_no")
                            ? "#f5222d"
                            : "#d9d9d9"
                        }}
                      />
                    )}
                  </Col>
                </Row>
              </Col>
              <Col span={12} style={{ paddingLeft: "15px" }}>
                <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
                  <Col
                    span={15}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <IntlMessages id="purchaseInvoices.exlDiscountAndTax" />
                  </Col>
                  <Col
                    span={8}
                    offset={1}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <span>
                      <NumberFormat value={data.total_exclude_discount_tax} />
                    </span>
                  </Col>
                </Row>

                <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
                  <Col
                    span={10}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <IntlMessages id="purchaseInvoices.discount" />{" "}
                    {this.state.activeCustDisc ? (
                      <Tooltip
                        placement="bottom"
                        // title={
                        //   <IntlMessages id="raptorSalesInvoices.getDiscount.help" />
                        // }
                      >
                        <StatusTag
                          className="billed-payment"
                          style={{ marginLeft: "4px" }}
                        >
                          {this.state.activeCustDisc}
                        </StatusTag>
                      </Tooltip>
                    ) : null}
                  </Col>
                  <Col span={5} style={{ paddingLeft: "7px" }}>
                    <Tooltip
                      placement="bottom"
                      title={this.state.fullDiscMultiplier || ""}
                    >
                      {getFieldDecorator("discount", {
                        initialValue: parseFloat(
                          data.discount_amount
                        ).toString()
                      })(
                        <Input
                          size="small"
                          disabled={true}
                          style={{ marginTop: "3px" }}
                        />
                      )}
                    </Tooltip>
                  </Col>
                  <Col
                    span={8}
                    offset={1}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <span>
                      <NumberFormat value={data.discount_amount} />
                    </span>
                  </Col>
                </Row>

                <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
                  <Col
                    span={15}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <IntlMessages id="purchaseInvoices.totalDiscount" />
                  </Col>
                  <Col
                    span={8}
                    offset={1}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <span>
                      <NumberFormat value={data.total_discount} />
                    </span>
                  </Col>
                </Row>

                <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
                  <Col
                    span={15}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <IntlMessages id="raptorSalesInvoices.service" />
                  </Col>
                  <Col
                    span={8}
                    offset={1}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <span>
                      <NumberFormat value={data.service_charge} />
                    </span>
                  </Col>
                </Row>

                <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
                  <Col
                    span={15}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <IntlMessages id="purchaseInvoices.totalTax" />
                  </Col>
                  <Col
                    span={8}
                    offset={1}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <span>
                      <NumberFormat value={data.total_tax} />
                    </span>
                  </Col>
                </Row>

                <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
                  <Col
                    span={15}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <IntlMessages id="raptorSalesInvoices.totalRounding" />
                  </Col>
                  <Col
                    span={8}
                    offset={1}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <span>
                      <NumberFormat value={data.total_rounding} />
                    </span>
                  </Col>
                </Row>

                <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
                  <Col span={24} style={{ display: "flex", marginTop: "5px" }}>
                    <Col
                      span={15}
                      style={{ margin: "auto 0 auto 0", textAlign: "right" }}
                    >
                      <Tooltip
                        placement="top"
                        title={
                          <IntlMessages id="raptorSalesInvoices.helpSetPaymentTerm" />
                        }
                      >
                        <strong>
                          <IntlMessages id="total" />
                        </strong>
                        <Icon
                          style={{ marginLeft: "5px" }}
                          type="question-circle"
                          theme="filled"
                        />
                      </Tooltip>
                      {/* <Button>
                        <IntlMessages id="total" />
                        <Icon type="edit" />
                      </Button> */}
                    </Col>
                    <Col span={1} />
                    <Col
                      span={8}
                      // offset={1}
                      style={{ margin: "auto 0 auto 0", textAlign: "right" }}
                    >
                      <Button
                        block
                        style={{ textAlign: "right", padding: "0 5px" }}
                        onClick={this.handleModalMultiPayment}
                        type={
                          this.state.errPayment.length > 0
                            ? "danger"
                            : "default"
                        }
                      >
                        <NumberFormat value={data.total_amount} />
                        <Icon type="edit" />
                      </Button>
                    </Col>
                  </Col>
                </Row>
              </Col>
            </Row>
          </TabPane>

          <TabPane
            tab={<IntlMessages id="jobCostings.tab.attachment" />}
            key="2"
          >
            <RaptorSalesInvoiceDocUpdateForm
              raptorSalesInvoice={this.props.raptorSalesInvoice}
              params={this.props.params}
              form={this.props.form}
            />
          </TabPane>
        </Tabs>

        {this.state.tab !== "2" ? (
          <Form.Item {...tailFormItemLayout}>
            <div
              style={{
                alignItems: "center",
                display: "flex",
                marginTop: "24px"
              }}
            >
              {/* Confirm */}
              <Popconfirm
                title={<IntlMessages id="confirmTransaction" />}
                onConfirm={() => {
                  this.props.form.validateFieldsAndScroll((err, values) => {
                    const { payments } = this.state.paymentDetails;
                    // validasi payment term
                    let index = [];
                    payments.map((payment, key) => {
                      if (
                        payment.payment_type === 2 &&
                        !payment.id_payment_term
                      ) {
                        return index.push(key);
                      }
                    });
                    if (index.length > 0) {
                      return this.setState({ errPayment: [...index] });
                    } else {
                      this.setState({ errPayment: [] });
                    }

                    let body = Object.assign({}, this.state.paymentDetails);

                    if (!err) {
                      this.setState({ err: false });
                      return this.props.confirmRaptorSalesInvoice(
                        data.id_sales_invoice,
                        body
                      );
                      // console.log("body", body);
                    } else {
                      return this.setState({ err: true });
                    }
                  });
                }}
                okText={<IntlMessages id="yes" />}
                cancelText={<IntlMessages id="no" />}
              >
                <Button
                  style={{ marginRight: "5px", padding: "0px 15px" }}
                  disabled={
                    data.status > 1 ||
                    this.state.not_exists > 0 ||
                    this.state.errPayment.length > 0 ||
                    !permissions.includes("Confirm Raptor Sales Invoice")
                      ? true
                      : false
                  }
                  loading={loading || loadingConfirm}
                >
                  <IntlMessages id="button.confirmTransaction" />
                </Button>
              </Popconfirm>

              {/* Cancel */}
              <Button
                style={{ marginRight: "5px", padding: "0px 15px" }}
                disabled={
                  data.status !== 2 ||
                  !permissions.includes("Unconfirm Raptor Sales Invoice")
                    ? true
                    : false
                }
                loading={loading || loadingCancel}
                onClick={this.handleModalCancel}
              >
                <IntlMessages id="button.unconfirmTransaction" />
              </Button>
            </div>

            <br />

            {this.state.err ? (
              <span style={{ color: "#f5222d" }}>
                <IntlMessages id="form.error" />
              </span>
            ) : null}

            {this.state.errPayment.length > 0 ? (
              <span style={{ color: "#f5222d" }}>
                <IntlMessages id="errorPaymentTerm" />
              </span>
            ) : null}
          </Form.Item>
        ) : null}
      </Form>
    );
  }
}

const WrappedSalesInvoiceUpdateForm = Form.create({
  name: "edit_raptor_sales_invoice"
})(RaptorSalesInvoiceUpdateForm);

const mapStateToProps = state => {
  return {
    user: state.auth.user,
    auth: state.auth
  };
};

export default compose(
  connect(mapStateToProps, {
    setPrintData,
    confirmRaptorSalesInvoice,
    cancelRaptorSalesInvoice,
    unconfirmRaptorSalesInvoice
  })
)(WrappedSalesInvoiceUpdateForm);
